<template>
  <div class="container">
    <h2>Manager für angelegte Arbeiten</h2>
    <div
      class="alert alert-success"
      role="alert"
    >
      Wissenschaftliche Betreuer, sowie unternehmensinterne Ansprechpartner können den Studenten im jeweiligen Assignment zugeordnet werden.
    </div>
    <table
      class="table"
      v-for="course in courses"
      :key="course.id"
    >
      <thead>
        <tr>
          <th scope="col">
            Assignment ID
          </th>
          <th scope="col">
            Assignment CMID
          </th>
          <th scope="col">
            Kurs
          </th>
          <th scope="col">
            Assignment Name
          </th>
          <th scope="col">
            Abgabe
          </th>
          <th scope="col">
            Studenten
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="assignment in course.assignments"
          :key="assignment.id"
        >
          <td>{{ assignment.id }}</td>
          <td>{{ assignment.cmid }}</td>
          <td>{{ course.fullname }}</td>
          <td>{{ assignment.name }}</td>
          <td>{{ assignment.duedate }}</td>
          <td>
            <button
              class="btn btn-primary"
              type="button"
              @click="showStudents(assignment.course)"
            >
              Studenten
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <div
      class="modal fade"
      id="modalStudents"
      ref="modalStudents"
      tabindex="-1"
      role="dialog"
      aria-labelledby="modalStudents"
      aria-hidden="true"
    >
      <div
        class="modal-dialog modal-lg"
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">
              Diesem Assignment zugewiesene Studenten
            </h5>
          </div>
          <div class="modal-body">
            <div class="container">
              <table class="table table-responsive">
                <thead>
                  <tr>
                    <th scope="col">
                      Student ID
                    </th>
                    <th scope="col">
                      Student Name
                    </th>
                    <th scope="col">
                      Aktueller Wiss. Betreuer
                    </th>
                    <th scope="col">
                      Neuer Wiss. Betreuer
                    </th>
                    <th scope="col">
                      Unternehmensbetreuer
                    </th>
                    <th scope="col">
                      Geändert
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="student in students"
                    :key="student.id"
                  >
                    <td>{{ student.id }}</td>
                    <td>{{ student.firstname }} {{ student.lastname }}</td>
                    <td />
                    <div class="form-group">
                      <select
                        name="supervisors"
                        id="supervisors"
                        class="form-control form-control-sm mt-2"
                        @change="student.changed=true"
                      >
                        <option value="NULL" />
                        <option
                          v-for="supervisor in supervisors"
                          :key="supervisor.id"
                          :value="supervisor.id"
                        >
                          {{ supervisor.firstname }} {{ supervisor.lastname }}
                        </option>
                      </select>
                    </div>

                    <td>{{ }}</td>
                    <td>{{ ((student.changed) ? 'Geändert' : '') }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-primary"
              data-dismiss="modal"
            >
              Änderungen sichern
            </button>
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Schließen
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
export default {
  name: "About",
  components: {},
  data() {
    return {
      students: [],
      showModal: false,
      supervisors: []
    };
  },
  props: {
    courses: {
      type: Array,
      default: function () {
        return []
      }
    },
    token: {
      type: String,
      default: ""
    }
  },
  beforeCreate: function() {},
  mounted() {
    this.getSupervisors();
  },
  methods: {
    showStudents(id) {
      this.$emit("loading", "10%", false);
      this.students = [];
      var axiosConfig = {
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        }
      };
      axios
        .get(
          (process.env.VUE_APP_MOODEL_URL || "") +
            "/webservice/rest/server.php",
          {
            params: {
              wstoken: this.token,
              wsfunction: "core_enrol_get_enrolled_users",
              moodlewsrestformat: "json",
              courseid: id
            }
          },
          axiosConfig
        )
        .then(response => {
          if (!response.exception) {
            this.students = this.filterStudents(response.data);
            console.log(this.students); // eslint-disable-line
            $("#modalStudents").modal("show");
            this.$emit("loading", "100%", true);
          }
        })
        .catch(error => {
          console.log(error); // eslint-disable-line
          this.$emit("loading", "100%", true);
          this.$emit("ajaxerror");
        });
    },
    getSupervisors() {
      this.$emit("loading", "10%", false);
      this.supervisors = [];
      var axiosConfig = {
        headers: {
          "Content-Type": "application/json;charset=UTF-8"
        }
      };
      axios
        .get(
          (process.env.VUE_APP_MOODEL_URL || "") +
            "/webservice/rest/server.php",
          {
            params: {
              wstoken: this.token,
              wsfunction: "local_spsupman_get_supervisors",
              moodlewsrestformat: "json"
            }
          },
          axiosConfig
        )
        .then(response => {
          if (!response.data.exception) {
            this.supervisors = response.data;
            this.$emit("loading", "100%", true);
          } else {
            this.$emit("loading", "100%", true);
            this.$emit("ajaxerror");
          }
        })
        .catch(error => {
          console.log(error); // eslint-disable-line
          this.$emit("loading", "100%", true);
          this.$emit("ajaxerror");
        });
    },
    filterStudents(allStudents) {
      var filteredStudents = [];
      for (let i = 0; i < allStudents.length; i++) {
        var isStudent = false;
        for (let j = 0; j < allStudents[i].roles.length; j++) {
          if (allStudents[i].roles[j].roleid == 5) {
            isStudent = true;
          }
        }
        if (isStudent) {
          allStudents[i].changed = false;
          filteredStudents.push(allStudents[i]);
        }
      }
      return filteredStudents;
    }
  }
};
</script>


